function toggleCases() {
  $('.heading__button').on('click', function() {
    $('.heading__button').removeClass('active');
    $(this).addClass('active');
    
    const filterValue = $(this).data('filter');
    
    if (filterValue === 'all') {
      $('.case').show();
    } else {
      $('.case').hide();
      $(`.case[data-filter*="${filterValue}"]`).show();
    }
  });
}
toggleCases();